/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-i18n/
 */
import { __ } from '@wordpress/i18n';

/**
 * React hook that is used to mark the block wrapper element.
 * It provides all the necessary props like the class name.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-block-editor/#useblockprops
 */
import { useBlockProps } from '@wordpress/block-editor';

import { useEffect } from '@wordpress/element';

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
import './editor.scss';

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/block-api/block-edit-save/#edit
 *
 * @return {WPElement} Element to render.
 */

import { useState, Fragment } from '@wordpress/element';
import { InspectorControls, BlockControls, AlignmentControl, PanelColorSettings } from '@wordpress/block-editor';
import { RangeControl, PanelBody } from '@wordpress/components';

export default function Edit( props ) {
	const { setAttributes, attributes } = props;
	const { blockId, textAlign, textColor, linkColor, separatorColor, textSize  } = attributes;

	const generateUniqueId = ( length = 10 ) => {
        const charset = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
		let randomString = '';
		for (let i = 0; i < length; i++) {
			const randomIndex = Math.floor(Math.random() * charset.length);
			randomString += charset[randomIndex];
		}
		return randomString;
    };

	// Set a unique block ID if not already set
    useEffect(() => {
        if (!blockId) {
            setAttributes({ blockId: `templazee-breadcrumb-${generateUniqueId()}` });
        }
    }, [blockId, setAttributes]);

	const linkColorSettings = [
		{
			value: linkColor,
			onChange: colorValue => setAttributes({ linkColor: colorValue }),
			label: __( "Link Color", '' )
		}
	];

	const textColorSettings = [
		{
			value: textColor,
			onChange: colorValue => setAttributes({ textColor: colorValue }),
			label: __( "Text Color", '' )
		}
	];

	const separatorColorSettings = [
		{
			value: separatorColor,
			onChange: colorValue => setAttributes({ separatorColor: colorValue }),
			label: __( "Text Color", '' )
		}
	];


	return (
		<div { ...useBlockProps() }>
			<Fragment>
				<BlockControls>
					<AlignmentControl
						value={textAlign}
						onChange={ ( nextAlign ) => {
							setAttributes( { textAlign: nextAlign } );
						} }
					/>
				</BlockControls>
				<InspectorControls>
					<PanelBody title={ __( 'Breadcrumb Options', '' ) } >
						<RangeControl
							label={__('Font Size', '')}
							onChange={ (value) => setAttributes( { textSize: value } ) }
							shiftStep={ 1 }
							min={ 2 }
							max={ 100 }
							value={ textSize }
						/>
					</PanelBody>
					<PanelColorSettings
						title={__('Link Color', '')}
						colorSettings={linkColorSettings}
						initialOpen={false}
					/>
					<PanelColorSettings
						title={__('Text Color', '')}
						colorSettings={textColorSettings}
						initialOpen={false}
					/>
					<PanelColorSettings
						title={__('Separator Color', '')}
						colorSettings={separatorColorSettings}
						initialOpen={false}
					/>
				</InspectorControls>
			</Fragment>
			<div id="templazee-breadcrumb-block" className="templazee-wrapper" style={ {textAlign: textAlign } }>
					<ul className="trail-items" itemscope="templazee" style={ {fontSize: textSize, color:separatorColor } }>
							<li itemprop="itemListElement" itemscope="templazee" className="trail-item trail-begin"><a href="#" rel="home" itemprop="item" style={ {color: linkColor } }><span itemprop="name">Home</span></a></li>
							<li itemprop="itemListElement" itemscope="templazee" className="trail-item trail-end"><span itemprop="item" style={ {color: textColor } } ><span itemprop="name">Page Title</span></span></li>
					</ul>
			</div>
		</div>
	);
}
